import React from 'react';

import './links.scss';

const Links = () => {
	return (
		<div className={'links'}>
			<a href="https://codepen.io/isconst808/" target="_blank" rel="noopener noreferrer" className={"links__btn"}>
				<i className="fab fa-codepen" />
			</a>
			<a href="https://github.com/isconst808" target="_blank" rel="noopener noreferrer" className={"links__btn"}>
				<i className="fab fa-github" />
			</a>
		</div>
	);
};

export default Links;