import React, {Component} from 'react';
import {CopyButton} from 'react-copy-button';

import './contacts.scss';

class Contacts extends Component {

	constructor(props) {
		super(props);
		this.state = {
			phone: "+7(977)852-07-91",
			email: "isconst808@gmail.com",
			text: '',
			classActive: false
		};

		this.timerId = 0;
	}


	btnEvent = (e) => {
		clearTimeout(this.timerId);
		this.setState({
			text: e.target.dataset.value,
			classActive: true
		});
		this.timerId = setTimeout(()=> {
			this.setState({
				text: '',
				classActive: false
			});
		}, 3000);

	};

	render() {

		const { phone, email, text, classActive } = this.state;

		return (
			<section className={'contacts'}>
				<div className="contacts__btn-block">
					<CopyButton className={"contacts__btn"} text="+79778520791" >
						<a href="tel:+79778520791" onClick={this.btnEvent} className={"contacts__btn-inner"} data-value={phone}>
							<i className="fas fa-phone-alt contacts__btn-icon" data-value={phone}>
							</i>
						</a>
					</CopyButton>
					<CopyButton className={"contacts__btn"} text="isconst808@gmail.com" >
						<div onClick={this.btnEvent} className={"contacts__btn-inner"} data-value={email}>
							<i className="fas fa-envelope contacts__btn-icon" data-value={email} />
						</div>
					</CopyButton>
					<a href="https://t.me/isconst808" target="_blank" rel="noreferrer noopener" className={"contacts__btn"}>
						<div className={"contacts__btn-inner"}>
							<i className="fab fa-telegram-plane contacts__btn-icon" />
						</div>
					</a>
					<p className={`contacts__msg ${(classActive) ? 'active' : ''}`}>
						{text}
						<span>скопирован в буфер обмена</span>
					</p>
				</div>
			</section>
		);
	}
}

export default Contacts