import React, {Component} from 'react';
import { connect } from "react-redux";


import './accordion.scss';
import Skills from "../skills";
import Contacts from "../contacts";
import { bindActionCreators } from "redux";
import {changeAccordionContent, closeAccordion} from "../../actions";
import { removeActiveClassBtnMenu } from '../../utils';

class Accordion extends Component {

	renderAccordionContent = () => {
		switch (this.props.reducerAccordionContent) {
			case 'skills':
				return <Skills />;
			case 'contacts':
				return <Contacts />;
			default:
				return null;
		}
	};

	onCloseAccordion = () => {
		removeActiveClassBtnMenu(this.props.reducerChildrenBtnMenu);
		this.props.closeAccordion();
		setTimeout(()=> {
			this.props.changeAccordionContent('');
		}, 700)
	};

	render() {
		const activeClass = ( this.props.reducerAccordion ) ? 'active' : '';

		return (
			<div className={`accordion`}>
				<div className="container accordion__container">
					<div className={`accordion__content ${activeClass}`}>
						{ this.renderAccordionContent() }
					</div>
					<button className={`close-btn accordion__btn-close`} onClick={this.onCloseAccordion}>
						<span className="close-btn__inner" />
					</button>
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		reducerAccordion: state.reducerAccordion,
		reducerAccordionContent: state.reducerAccordionContent,
		reducerChildrenBtnMenu: state.reducerChildrenBtnMenu
	}
};

const mapDispatchToProps = dispatch => bindActionCreators({
	closeAccordion,
	changeAccordionContent
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Accordion)