export const removeActiveClassBtnMenu = (children) => {
	const arrChildren = children;
	for(let n = 0; n < arrChildren.length; n++) {
		const arrClass = arrChildren[n].className.split(' ');
		const indexClassActive = arrClass.findIndex((item) => item === 'active');
		if(indexClassActive !== -1) {
			arrClass.splice(indexClassActive, 1);
			arrChildren[n].className = arrClass.join(' ');
			break;
		}
	}
};

export const addActiveClassBtnMenu = (btn) => {
	const arrClass = btn.className.split(' ');
	arrClass.push('active');
	btn.className = arrClass.join(' ');
};