import React, {Component} from 'react';
import {connect} from 'react-redux';
import './menu.scss';
import {bindActionCreators} from "redux";
import {openAccordion, closeAccordion, changeAccordionContent, getChildrenBtnMenu} from "../../actions";

import { removeActiveClassBtnMenu, addActiveClassBtnMenu } from '../../utils';

class Menu extends Component {
	constructor(props) {
		super(props);
		// создадим реф в поле `textInput` для хранения DOM-элемента
		this.parentButtons = React.createRef();
	}

	componentDidMount() {
		this.props.getChildrenBtnMenu(this.parentButtons.current.children);
	}

	openAccordion = (e) => {

		const btnDataMenu = e.target.dataset.menu,
			btnMenu = e.target,
			{ reducerAccordionContent, reducerChildrenBtnMenu} = this.props;

		if(reducerAccordionContent !== btnDataMenu) {
			this.props.closeAccordion();
			removeActiveClassBtnMenu(reducerChildrenBtnMenu);
			addActiveClassBtnMenu(btnMenu);
			setTimeout(()=> {
				this.props.changeAccordionContent(btnDataMenu);
				this.props.openAccordion();
			}, 800)
		}
	};


	render() {
		return (
			<div className={'menu'} ref={this.parentButtons}>
				<button onClick={this.openAccordion} data-menu="skills" className={'menu__btn'}>Навыки</button>
				<button onClick={this.openAccordion} data-menu="contacts" className={'menu__btn'}>Контакты</button>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		reducerAccordionContent: state.reducerAccordionContent,
		reducerChildrenBtnMenu: state.reducerChildrenBtnMenu
	}
};

const mapDispatchToProps = dispatch => bindActionCreators({
	openAccordion,
	closeAccordion,
	changeAccordionContent,
	getChildrenBtnMenu
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Menu)