import React, {Component} from 'react';

import './modal.scss';
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import { closeModal, closeModalContent } from '../../actions';
import ModalContent from "../modal-content";


class Modal extends Component {

	onCloseModel = (e) => {
		if(e.target.dataset.name === 'modal') {
			this.props.closeModal();
			this.props.closeModalContent();
		}
	};


	render() {

		const active = ( this.props.modalActive ) ? 'active' : '';

		return (
			<div className={`is-modal ${active}`} onClick={this.onCloseModel} data-name="modal">
				<div className="is-modal__block container" onClick={this.onCloseModel} data-name="modal">
					<div className="is-modal__inner" data-name="modal">
						<button className="close-btn is-modal__btn-close" data-name="modal">
							<span className="close-btn__inner"/>
						</button>
						<ModalContent />
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {
		modalActive: state.reducerModal,
		modalContent: state.reducerModalContent
	}
};

const mapDispatchToProps = dispatch => bindActionCreators({
	closeModal,
	closeModalContent
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Modal)