import { combineReducers } from 'redux'
import reducerDB from './reducer-db';
import reducerModal from './reduce-modal'
import reducerModalContent from './reduce-modal-content';
import reducerAccordion from	'./reduce-accordion';
import reducerAccordionContent from './reduce-accordion-content';
import reducerChildrenBtnMenu from './reduce-menu';

export default combineReducers({
	reducerDB,
	reducerModal,
	reducerModalContent,
	reducerAccordion,
	reducerAccordionContent,
	reducerChildrenBtnMenu
})