import * as types from '../actionType';

export const openAccordion = () => ({
	type: types.OPEN_ACCORDION,
	payload: true
});

export const closeAccordion = () => ({
	type: types.CLOSE_ACCORDION,
	payload: false
});

export const changeAccordionContent = (value) => ({
	type: types.CHANGE_ACCORDION_CONTENT,
	payload: value
});